import React from "react";
import moment from "moment";
import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';
import {formatDate, parseDate,} from 'react-day-picker/moment';
import "react-day-picker/lib/style.css";

class ExpenseForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            description: props.expense ? props.expense.description : '',
            note: props.expense ? props.expense.note : '',
            amount: props.expense ? props.expense.amount.toString() : '',
            createdAt: props.expense ? moment(props.expense.createdAt) : moment(),
            error: '',
        }
    }
    onDescriptionChange = (e) => {
        const description = e.target.value;
        this.setState(() => ({description}));
    }
    onNoteChange = (e) => {
        const note = e.target.value;
        this.setState(() => ({note}));
    }
    onAmountChange = (e) => {
        const amount = e.target.value;
        if (!amount || amount.match(/^\d+(\.\d{0,2})?$/)) {
            this.setState(() => ({amount}));
        }
    }
    onDateChange = (createdAt) => {
        if (createdAt) {
            this.setState(() =>({createdAt: moment(createdAt).unix()}))
        }
    }
    onSubmit = (e) => {
        e.preventDefault();
        if (!this.state.description || !this.state.amount) {
            this.setState(() => ({error: 'Please provide description and amount'}));
        } else {
            this.setState(() => ({error: ''}));
            this.props.onSubmit({
                description: this.state.description,
                amount: (parseFloat(this.state.amount)).toFixed(2),
                createdAt: this.state.createdAt.valueOf(),
                note: this.state.note,
            });
        }
    }

    render() {
        return (
                <form className="form" onSubmit={this.onSubmit}>
                    {this.state.error && <p className="form__error">{this.state.error}</p>}
                    <input
                        className="text-input"
                        type="text"
                        placeholder="Description"
                        autoFocus
                        value = {this.state.description}
                        onChange={this.onDescriptionChange}
                    />

                    <input
                        className="text-input"
                        type="number"
                        placeholder="Amount"
                        value={this.state.amount}
                        onChange={this.onAmountChange}
                    />

                    <DayPickerInput
                        inputProps={{ className: 'text-input', id: 'calendar' }}
                        formatDate={formatDate}
                        parseDate={parseDate}
                        value={moment.unix(this.state.createdAt).format("MM/DD/YY")}
                        onDayChange={this.onDateChange}
                    />

                    <textarea
                        className="text-input"
                        id="notes"
                        placeholder="Add note for yout expense (optional)"
                        value={this.state.note}
                        onChange={this.onNoteChange}
                    >
                    </textarea>
                    <div>
                        <button className="button">Save Expense</button>
                    </div>
                </form>
        )
    }
}

export default ExpenseForm;
